/* eslint-disable @typescript-eslint/no-unused-vars */
import { NextResponse } from "next/server";
import OpenAI from "openai";
import { Client } from "@upstash/qstash";

// Initialize QStash client
const qstashClient = new Client({
  token: process.env.QSTASH_TOKEN || "",
});

export async function POST(request: Request) {
  try {
    // Parse the request body
    const body = await request.json();
    const { text } = body;

    // Initialize OpenAI client with API key from environment variable
    const openai = new OpenAI({
      apiKey: process.env.OPENAI_API_KEY,
    });

    const promptTranca = `
      Necesito que hagas un resumen de la siguiente entrada:
      ${text}

      Sigue la siguiente estructura:
      Resumen: 
      [resumen]

      Puntos clave:
      [puntos clave]


      Formato html, usa negritas, cursivas, listas, etc para que el resumen sea más legible.
      estiliza el texto con inner css para que los título (resumen y puntos clave) sean más visibles.
    `;

    const promptHardcore = `
      Necesito que hagas un resumen de la siguiente entrada:
      ${text}

      Sigue la siguiente estructura:
      Resumen: 
      [resumen]

      Puntos clave:
      [puntos clave]

      Análisis:
      [necesito que hagas un análisis de la entrada, que vaya desde sentimientos de quien lo escribe hasta un desgloce completo
      en qué sirve esta información para entenderla psicología del autor, el resumen debe ser extenso y detallado]

      Formato html, usa negritas, cursivas, listas, etc para que el resumen sea más legible.
    `;

    // Call OpenAI API
    const chatCompletion = await openai.chat.completions.create({
      messages: [
        {
          role: "system",
          content:
            "Eres un asistente experto en hacer resúmenes detallados pero cortos.",
        },
        { role: "user", content: promptTranca },
      ],
      model: "gpt-3.5-turbo",
    });

    // Get the response from OpenAI
    const aiResponse = chatCompletion.choices[0].message.content;

    // Return the response
    return NextResponse.json({ message: aiResponse });
  } catch (error) {
    console.error("API Error:", error);
    return NextResponse.json(
      { error: "Failed to process OpenAI request" },
      { status: 500 }
    );
  }
}

// New endpoint for QStash test message
export async function PUT(request: Request) {
  try {
    const body = await request.json();
    const { message, destination } = body;

    if (!message || !destination) {
      return NextResponse.json(
        { error: "Message and destination are required" },
        { status: 400 }
      );
    }

    // Send a message to QStash
    const response = await qstashClient.publishJSON({
      url: destination,
      body: {
        message: message,
        timestamp: new Date().toISOString(),
      },
    });

    return NextResponse.json({
      success: true,
      messageId: response.messageId,
      message: "Message queued successfully",
    });
  } catch (error) {
    console.error("QStash Error:", error);
    return NextResponse.json(
      { error: "Failed to send message to QStash" },
      { status: 500 }
    );
  }
}
